import * as React from 'react';
import { ethers } from "ethers";
import { Typography, Skeleton, Stack } from '@material-ui/core';
import { ChainID, Provider, Signer, Address } from './metamaskButton.js';
import { ColorButton, ColorStack } from './widgets.js';
import { blockToDate } from '../util/ethutils.js';
import { LoadingRow } from './loadingRow.js';
import { NumberField } from './numberField.js';

const chain = 5777
// const address = require('../contracts/IFO.json').networks[chain].address
// const addressr = require('../contracts/BUSD.json').networks[chain].address
// const addresso = require('../contracts/PART.json').networks[chain].address
const address = "0x625e5b02734378402bb20e69dc8ca151b9d97970"
const addressr = "0x0d500b1d8e8ef31e21c99d1db9a6444d3adf1270"
const addresso = "0x840b5fc8c6dee2b1140174a3abdc215190426ccf"
const abi = require('../contracts/IFO.json').abi
const tokenAbi = require('../contracts/BUSD.json').abi

export default function IFO(props) {

    const [state, setState] = React.useState("null")
    const [ifoState, setIfoState] = React.useState("null")
    const [startBlock, setStartBlock] = React.useState(null)
    const [endBlock, setEndBlock] = React.useState(null)
    const [offering, setOffering] = React.useState(null)
    const [raising, setRaising] = React.useState(null)
    const [raised, setRaised] = React.useState(null)
    const [symbolo, setSymbolo] = React.useState(null)
    const [symbolr, setSymbolr] = React.useState(null)
    const [allocation, setAllocation] = React.useState(null)
    const [claim, setClaim] = React.useState(null)
    const [deposit, setDeposit] = React.useState(0)
    const [maxDeposit, setMaxDeposit] = React.useState(0)

    React.useEffect(() => {
        setTimeout(update, 1000)
        const interval = setInterval(update, 10000)
        return () => clearInterval(interval);
    }, [])

    const update = async () => {
        if (Provider == null) {
            setState("init")
            return
        }

        // if (ChainID !== chain) {
        //     setState("wrong_id")
        //     return
        // }

        setState("ready")
        
        const contract = new ethers.Contract(address, abi, Provider)
        const tokeno = new ethers.Contract(addresso, tokenAbi, Provider)
        const tokenr = new ethers.Contract(addressr, tokenAbi, Provider)
        
        // Set dates from start and end blocks
        const startBlock = parseInt(await contract.startBlock())
        const endBlock = parseInt(await contract.endBlock())
        setStartBlock(await blockToDate(startBlock))
        setEndBlock(await blockToDate(endBlock))
        
        // Get Allocation
        try {
            const alloc = await contract.getAmount(Address)
            setAllocation(ethers.utils.formatEther(alloc))
        } catch(error) {
            setAllocation(0)
        }
        
        // Get token symbols
        const so = await tokeno.symbol()
        const sr = await tokenr.symbol()
        setSymbolo(so)
        setSymbolr(sr)

        // Set raising and offering
        const r = ethers.utils.formatEther(await contract.raisingAmount())
        setRaising(r + " " + sr)
        setOffering(ethers.utils.formatEther(await contract.offeringAmount()) + " " + so)
        
        // Calculate the raised percentage
        const total = ethers.utils.formatEther(await contract.totalAmount())
        setRaised((parseFloat(total) / parseFloat(r)) * 100)

        // Check if IFO has yet to start, it's in progress, or finished
        const currentBlock = await Provider.getBlockNumber()
        if (currentBlock < startBlock) {
            setIfoState("waiting")
            return
        } else if (currentBlock >= endBlock - 1) {
            setIfoState("finished")
            
            const harvest = await contract.hasHarvest(Address)
            if (harvest === true) {
                setClaim(null)
                return
            }

            // Check claim
            try {
                setClaim(ethers.utils.formatEther(await contract.getOfferingAmount(Address)))
            } catch(error) {
                setClaim(0)
            }

            return
        }
        
        // Check raising token approval
        const allowance = parseInt(await tokenr.allowance(Address, address))

        if (allowance <= 0) {
            setIfoState("approval")
            return
        }

        setMaxDeposit(parseFloat(ethers.utils.formatEther(await tokenr.balanceOf(Address))))

        setIfoState("approved")
    }

    const approve = async () => {
        const tokenr = new ethers.Contract(addressr, tokenAbi, Signer)
        tokenr.approve(address, "0xFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF")
    }
    
    const makeDeposit = async () => {
        const contract = new ethers.Contract(address, abi, Signer)
        const t = ethers.utils.parseEther(deposit.toString())
        contract.deposit(t)
    }

    const makeClaim = async () => {
        const contract = new ethers.Contract(address, abi, Signer)
        contract.harvest()
    }

    const ifo = () => {
        if (state !== "ready") return
        if (ifoState === "null") {
            return <Skeleton variant="rect" width="90%" height="10%"/>
        } else if (ifoState === "waiting") {
            return <Typography mb="8px" variant="h5">Not yet started</Typography>
        } else if (ifoState === "finished") {
            return <Stack width="100%" direction="column" alignItems="center" justifyContent="center">
                <Typography variant="h5">IFO finished</Typography>
                {claim !== null ?
                    <ColorButton sx={{mr: "8px", mb: "8px", mt: "8px"}} onClick={makeClaim} size="small" variant="outlined">Claim {claim} {symbolo}</ColorButton>
                    : null
                }
            </Stack>
        } else if (ifoState === "approval") {
            return <ColorButton sx={{marginTop: "8px", marginBottom: "8px"}} onClick={approve} size="small" variant="outlined">Approve {symbolr}</ColorButton>
        } else if (ifoState === "approved") {
            return <NumberField value={deposit} setValue={setDeposit} maxValue={maxDeposit} click={makeDeposit} label="Deposit"/>
        }
    }

    const content = () => {
        if (state === "null") {
            return <Skeleton variant="rect" width="90%" height="25%"/>
        } else if (state === "init") {
            return <Typography variant="h5">Please connect wallet</Typography>
        } else if (state === "wrong_id") {
            return <Typography variant="h5">Incorrect Network</Typography>
        } else if (state === "ready") {
            return <Stack width="100%" direction="column">
                <LoadingRow label="Launch Time" value={startBlock}/>
                <LoadingRow label="End Time" value={endBlock}/>
                <LoadingRow label="For Sale" value={offering}/>
                <LoadingRow label="To Raise" value={raising}/>
                <LoadingRow label="Total Raised" value={raised} suffix="%"/>
                <LoadingRow label="Your Deposit" value={allocation} suffix={" " + symbolr}/>
            </Stack>
        }
    }

    return (
        <ColorStack width={props.width} height={props.height} direction="column" alignItems="center" justifyContent="flex-start">
            <img style={{marginTop: "8px", marginBottom: "8px"}} width="50%" src="logo512.png" alt=""/>
            {ifo()}
            {content()}
            <Typography color="yellow" align="justify" mt="8px" ml="8px" mr="8px" mb="8px" variant="body1">polyGAS Presale - Token Cost 1000 WMATIC per 1,000,000,000 Tokens. Launch date is 9/3. Buying presale ensures you are paying no tax on tokens. 100% of funds added to initial liquidity.</Typography>
        </ColorStack>
    )

}