import { Stack, InputAdornment } from "@material-ui/core";
import { ColorTexField, ColorButton } from "./widgets";


// Props: value, setValue, maxValue, click, label
export function NumberField(props) {
    return (
        <Stack margin="8px" direction="row" alignItems="center" justifyContent="space-between">
            <ColorTexField
                type="number"
                InputProps={{
                    endAdornment: <InputAdornment onClick={(e) => {props.setValue(props.maxValue)}} position="end">MAX</InputAdornment>
                }}
                value={props.value}
                onChange={(e) => {props.setValue(parseFloat(e.target.value))}}
                id="standard-basic"
                label={props.ticker === undefined ? "Amount" : props.ticker}
                variant="outlined"
                sx={{mr: "8px"}}
            />
            <ColorButton onClick={props.click} size="small" variant="outlined">{props.label}</ColorButton>
        </Stack>
    )
}