import * as React from 'react';
import { ColorButton } from './widgets.js'
import "react-native-get-random-values"
import "@ethersproject/shims"
import { ethers } from "ethers";
import { useMediaQuery } from 'react-responsive'

export var Provider = null
export var Signer = null
export var Address = null
export var ChainID = null

/**
 * props.id = Chain Number
 * props.rpc = Fallback RPC
 */
export default function MetamaskButton(props) {
    const [state, setState] = React.useState("install");
    const isPortrait = useMediaQuery({ query: '(orientation: portrait)' })
    const fallback = new ethers.providers.JsonRpcProvider(props.rpc)

    function ellipsize(address) {
        return address.substr(0, 4) + "â€¦" + address.substr(address.length - 4, address.length)
    }

    const tryChangeAddChain = async () => {
        const id = "0x" + parseInt(props.id).toString(16)
        try {
            await window.ethereum.request({ method: 'wallet_switchEthereumChain', params: [{ chainId: id }]})
            return true
        } catch (error) {
            if (error.code === 4902) {
                try {
                    await window.ethereum.request({method: 'wallet_addEthereumChain', params: [{ chainName: props.name, chainId: id, rpcUrls: [props.rpc] }]});
                    return true
                } catch (addError) {
                    console.log(addError)
                }
            }
            return false
        }
    }

    const getWallet = async (change) => {
        try {
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            Address = accounts[0]
            Provider = new ethers.providers.Web3Provider(window.ethereum)
            Signer = Provider.getSigner()
            ChainID = await window.ethereum.request({ method: 'eth_chainId' })
            if (parseInt(ChainID) !== parseInt(props.id)) {
                setState("network")
                if (change === true) await tryChangeAddChain()
            } else {
                setState("ready")
                window.localStorage.setItem("address", Address)
                document.dispatchEvent(new Event("wallet"))
            }
        } catch(error) {
            Provider = fallback
        }        
    }

    const handleClick = async () => {
        switch (state) {
            case "install": return window.open("https://metamask.io", '_blank')
            default: return await getWallet(true)
        }
    }

    const getText = () => {
        switch (state) {
            case "connect": return "CONNECT WALLET"
            case "install": return "INSTALL METAMASK"
            case "network": return "WRONG NETWORK"
            case "ready": return ellipsize(Address)
            default: return "Error"
        }
    }

    const onChainChange = async (chainId) => {
        document.dispatchEvent(new Event("ChainChange"))
        await getWallet(false)
        // window.location.reload()
    }

    const onAccountChange = async (accounts) => {
        document.dispatchEvent(new Event("AccountChange"))
        await getWallet(false)
    }

    React.useEffect(() => {
        const load = async () => {
            Provider = fallback
            window.ethereum.on("accountsChanged", onAccountChange)        
            window.ethereum.on("chainChanged", onChainChange)
            if (window.localStorage.getItem("address") != null) return await getWallet(document.hasFocus())
            setState(window.ethereum && window.ethereum.isMetaMask ? "connect" : "install")
        }
        load()
    }, [state])
    
    if (isPortrait) {
        return (<ColorButton {...props} onClick={handleClick} size="small" variant="outlined">{getText()}</ColorButton>);
    } else {
        return (<ColorButton {...props} onClick={handleClick} variant="outlined">{getText()}</ColorButton>);
    }
    
}