export default function AboutPage(props) {
    const isPortrait = useMediaQuery({ query: '(orientation: portrait)' })
    
    React.useEffect(() => {

    })

    const topMenu = () => {
        return (
            <Stack width="70%" direction="row" alignItems="center" justifyContent="space-between" spacing={4} p={1}>
                <PartnerMenu/>
                <Link to="/" style={{ color: 'inherit', textDecoration: 'inherit'}}>
                    <Stack direction="row">     
                        <Typography color={GreenColor} variant={isPortrait ? "h4" : "h3"}>Ether</Typography>
                        <Typography color={YellowColor} variant={isPortrait ? "h4" : "h3"}>{"&"}</Typography>
                        <Typography color={GreenColor} variant={isPortrait ? "h4" : "h3"}>Mites</Typography>
                    </Stack>
                </Link>
                <Stack direction="row" spacing={0}>
                    <ChainMenu items={contracts} id={props.id}/>
                    <MetamaskButton name={contracts[parseInt(props.id)].name} id={contracts[parseInt(props.id)].id} rpc={contracts[parseInt(props.id)].rpc}/>
                </Stack>
            </Stack>
        )
    }

    return (
        <Stack backgroundColor="#191919" minHeight="100%" direction="column" alignItems="center" justifyContent="flex-start">
            {topMenu()}
        </Stack>
    );
}