
import { Stack, Button, Switch, Radio, withStyles, TextField, styled } from '@material-ui/core';
import { Link } from "react-router-dom";
import { yellow } from '@material-ui/core/colors';

export const GreenColor = "#00ff7f"
export const YellowColor = yellow[400]

export const ColorButton = withStyles((theme) => ({
    root: {
        color: "#FFFFFFFF",
        borderColor: yellow[400],
        borderWidth: 2,
        borderRadius: 0,
        backgroundColor: "#FFFFFF00",
        '&:hover': {
            borderColor: yellow[400],
            borderWidth: 2,
            backgroundColor: "#FFFFFF11"
        }
    }
}))(Button)

export const ColorSwitch = withStyles({
    switchBase: {
        color: "#AAAAAAFF",
        '&$checked': {
            color: "#FFFFFFFF"
        },
        '&$checked + $track': {
            backgroundColor: "#FFFFFFFF"
        }
    },
    checked: {},
    track: {}
})(Switch);

export const ColorRadio = withStyles({
    root: {
      color: yellow[400],
      '&$checked': {
        color: yellow[600],
      },
    },
    checked: {},
})((props) => <Radio color="default" {...props} />);

  
export const ColorStack = withStyles({
    root: {
        borderColor: yellow[400],
        borderWidth: 2,
        borderRadius: 0,
        backgroundColor: "#111111",
        borderStyle: "solid"
    }
})(
    ({ classes, children, ...props }) => <Stack className={classes.root} {...props}>{children}</Stack>
)

export const ColorLink = styled(Link)({
    textDecoration: "inherit",
    color: "white",
    "&:hover": { color: yellow[400] }
})

export const ColorTexField = styled(TextField)({
    '& label.Mui-focused': {
        color: GreenColor,
    },
    '& .MuiInput-underline:after': {
        borderBottomColor: yellow[400],
    },
    '& .MuiOutlinedInput-root': {
        '&.Mui-focused fieldset': {
            borderColor: yellow[400],
        },
        '&:hover fieldset': {
            borderColor: yellow[400],
        },
        '& fieldset': {
            borderColor: yellow[400],
        }
    },
})
