import { Stack, Typography, Skeleton } from "@material-ui/core";


// Props: Label, Value | Optional Suffix, Prefix
export function LoadingRow(props) {
    return (
        <Stack ml="8px" mr="8px" direction="row" alignItems="center" justifyContent="space-between">
            <Typography color="#DDDDDD" variant="body2">{props.label}</Typography>
            {props.value !== null ? 
                <Typography fontWeight={700} variant="body2">{props.prefix}{props.value}{props.suffix}</Typography> : 
                <Skeleton variant="rect" width={100} height={16}/>}
        </Stack>
    )
}