export function randomInterval(min, max) {
    return Math.random() * (max - min + 1) + min;
}

export function toFixed(value, dp){
    return +parseFloat(value).toFixed(dp);
}

export function dateToString(timestamp) {
    try {
        const date = new Date(timestamp)
        const string = date.toUTCString()
        return string.split(", ")[1].split(" G")[0] + " UTC"
    } catch (error) {
        return "Invalid date"
    }
}
