import { Provider } from '../ui/metamaskButton.js';
import { dateToString } from "./utils.js";

export async function blockToDate(blockNumber) {

    try {
        const currentBlock = await Provider.getBlockNumber()

        if (currentBlock > blockNumber) {
            const block = await Provider.getBlock(blockNumber)
            return dateToString(block.timestamp * 1000)
        } else {
            const num = 1000
            const now = (await Provider.getBlock(currentBlock)).timestamp
            const past = (await Provider.getBlock(currentBlock - num)).timestamp
            const tpb = (now - past) / num * 1000
            const interval = blockNumber - currentBlock
            const finalTimestamp = Date.now() + (interval * tpb)
            return dateToString(finalTimestamp)
        }
    } catch (error) {
        return "Undefined"
    }
}
